@font-face {
  font-family: poppins;
  src: url(./font/Poppins/Poppins-Regular.ttf);
}
*{
  box-sizing: border-box;
  font-family: poppins;
}

body{
  margin: 0;
  padding: 0;
  background-color:  hsl(0, 100%, 74%);
  background-image: url(./images/bg-intro-desktop.png);
}
main {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
}
.top-content{
  width: 35%;
  height: fit-content;
  margin: auto;
}
h1{
  font-size: 43px;
  font-weight: 700;
  color: white;
}
.paragraph{
  font-size: 16px;
  font-weight: 400;
  color: rgb(241, 234, 234);
}
.bottom-content{
  width: 50%;
  margin-top: 5em;
}
.top-info{
  color: white;
  background-color: hsl(248, 32%, 49%);
  height: fit-content;
  border-radius: 10px;
  text-align: center;
  justify-content: center;
  font-size: 16px;
  width: 40vw;
  padding: 1em 3vw;
}
form{
  margin: unset;
  margin-top: 3vh;
  padding: 40px 2vw;
  width: 40vw;
  background-color: white;
  border-radius: 10px;
}
input{
  margin: 1vh auto;
  width: 34vw;
  padding: 2vh 2vw;
  font-size: 16px;
  border-radius: 5px;
  outline: none;
  border: 1px hsl(246, 25%, 77%);
  border-style: solid solid groove solid;
  font-weight: 700;
}

input.email::placeholder{
  color: hsl(0, 100%, 74%);
}

span.icon{
  font-size: 20px;
  color: hsl(0, 100%, 74%) ;
  float: right;
  right: 20px;
  top: -6vh;
  position: relative;
} 
span.InvalidDisplay i.error{
  float: right;
  font-weight: 700;
  color: hsl(0, 100%, 74%) ;
  font-size: 12px;
  padding-top: 0;
}
span.InvalidDisplay{
  display: none;
}
button{
  border-radius: 6px;
  outline: none;
  border: none;
  background-color: hsl(154, 59%, 51%);
  text-transform: uppercase;
  color: white;
  font-size: 16px;
  padding: 2vh 1vw;
  margin-top: 2vh;
  width: 34vw;
}
button:hover{
  opacity: 0.5;
}
.Terms{
  color: hsl(246, 25%, 77%);
  text-align: center;
  align-items: center; 
  font-size: 13px;
}
.Terms span{
  color: hsl(0, 100%, 74%) ;
  font-weight: 700;
}

@media only screen and (orientation:portrait){
  body{
    background-image: url(./images/bg-intro-mobile.png);
  }
  main {
    flex-direction: column;
  }
  .top-content{
    width: unset;
    margin: 3vh 2em 1em 2em;
    text-align: center;
  }
  h1{
    font-size: 25px;
  }

  .bottom-content{
    width: unset;
    margin: 1em 2em 2em 2em;
  }
  .top-info{
    height: fit-content;
    font-size: 14px;
    width: unset;
    padding: 1em 16vw;
  }
  form{
    margin: unset;
    margin-top: 2em;
    padding: 20px 20px;
    width: unset;
  }
  input{
    margin: 1vh auto;
    width: 100%;
    padding: 1.5vh 3vw;
    font-size: 13px;
  }
  span.InvalidDisplay i.error{
    font-size: 10px;
  }
  button{
    font-size: 12px;
    padding: 1.5vh 3vw;
    margin-top: 1vh;
    width: 100%;
  }
  .Terms{
    font-size: 11px;
  }
}

@media only screen and (orientation:portrait) and (min-width: 768px){
  .top-content{
    margin: 3vh 5em 1em 5em;
  }
  .top-info{
    font-size: 20px;
  }
  h1{
    font-size: 35px;
  }
  .paragraph{
    font-size: 20px;
  }
  .bottom-content{
    margin: 1em 5em 2em 5em;
  }
  input{
    font-size: 17px;
  }
  span.icon{
    font-size: 23px;
    top: -5vh;
  }
  span.InvalidDisplay i.error{
    font-size: 14px;
  }
  button{
    font-size: 17px;
  }
  .Terms{
    font-size: 15px;
  }
}

@media only screen and (orientation:landscape) and (max-width:900px) {

  main {
    flex-direction: column;
  }
  .top-content{
    width: unset;
    margin: 2vh 4em 0.5em 4em;
    text-align: center;
  }
  h1{
    font-size: 25px;
  }

  .bottom-content{
    width: unset;
    margin: 0.5em 4em 2em 4em;
  }
  .top-info{
    height: fit-content;
    font-size: 14px;
    width: unset;
    padding: 1em 5em;
  }
  form{
    margin: unset;
    margin-top: 1em;
    padding: 20px 30px;
    width: unset;
  }
  input{
    margin: 1vh auto;
    width: 100%;
    padding: 2.5vh 3vw;
    font-size: 13px;
  }
  span.icon{
      top: -10vh;
  }
  button{
    font-size: 12px;
    padding: 1.5vh 3vw;
    margin-top: 1vh;
    width: 100%;
  }
  .Terms{
    font-size: 11px;
  }
}
@media only screen and (orientation:landscape) and (min-width: 1500px) {
  .paragraph{
    font-size: 20px;
  }
  .top-info{
    font-size: 20px;
  }
  input{
    font-size: 20px;
  }
  .Terms{
    font-size: 15px;
  }
}
